import React, {Component} from 'react';
import Navigation from "../common/Navigation";
import Footer from "../common/Footer";
import Header from "../common/Header";

import '../../styles/contact-us.css';
import ContactTransperant from "./ContactTransperant";
import TeamComponent from "../team/TeamComponent";
import ContactForm from "./ContactForm";

export default class ContactPage extends Component {

    //TODO.... (Contact form doesn't work, it's only static!!! make it work)

    render = () => {
        return (
            <section>
                <Navigation {...this.props}/>
                <Header title="Get In Touch With Us"/>
                <TeamComponent/>

                <ContactTransperant title="Contact Us " description="Feel free to contact us"/>
                <ContactForm/>
                <br/><br/>

                <Footer/>
            </section>
        )
    }
}